import {defineStore} from  'pinia';
// 存储用户和机器人聊天的全局状态文件
export const useMainStore = defineStore('chat',{
        state:()=>{
            //对话数组
            let user = [];
            return{
                user
            }
        },
        
        getters:{
            
        },
        
        actions:{

            //给用户添加数据的方法
            addUser(data){
              this.user.push({type:0,text:data});
            },

            //给机器人添加数据的方法
            addRobot(data){
                this.user.push({type:1,text:data});
            }
            
        }
    
    });