// 聊天内容组件
<template>
  <div id="ChatContent">
      <div class="main">
          <div class="task">
              <template v-for="(item,index) in me" :key="index">
                <div class="message animate__animated animate__bounceInLeft wow" :class="{robot:item.type===1}">
                  <div class="picture">
                      <img v-if="item.type===0" src="@/assets/images/user.jpg">
                       <img v-else src="@/assets/images/robot.jpg">
                  </div>
                  <span>
                      {{item.text}}
                  </span>
              </div>
              </template>
          </div>
      </div>
  </div>
</template>

<script>
import {storeToRefs} from 'pinia';
import {useMainStore} from "@/store/index";
import {ref,reactive,onMounted,nextTick} from 'vue'
export default {
  name: "ChatContent",
  components: {
  },
  setup() {
    //实例化store
    const store = useMainStore();
    const {user:me} = storeToRefs(store);

    return {
        me
    }
  },
}
</script>

<style lang="less" scoped>
#ChatContent{
    flex: 1;
    background-color: #e8e8e87d;
    max-height: 610px;
    overflow-y: scroll;
    .main{
        padding: 1rem;
        .task{
            .message{
                display: flex;
                align-items: center;
                gap: 0.5rem;
                margin: 1rem 0;
                .picture{
                    width: 10%;
                    min-width: 50px;
                    max-width: 56px;
                    border-radius: 50%;
                    overflow: hidden;
                    cursor: pointer;
                }
                span{
                    color: #ffffff;
                    font-weight: bold;
                    text-shadow: 1px 2px 5px black;
                    white-space: pre-wrap;
                }
            }
           .robot{
               flex-direction: row-reverse;
           }
        }
    }
}
</style>
