// 网站首页
<template>
  <main>
    <div id="Home">
       <HomeHeader></HomeHeader>
       <ChatContent></ChatContent>
       <HomeFooter></HomeFooter>
    </div>
    <!-- 弹窗，提醒用户配置API -->
    <div class="dialog">
        <el-dialog
    v-model="dialog"
    title="请配置机器人API秘钥"
  >
    <el-input v-model.trim="api" placeholder="请在这里输入秘钥" />
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialog = false">取消</el-button>
        <el-button type="primary" @click="ckok">
          确定
        </el-button>
      </span>
    </template>
  </el-dialog>
    </div>
  </main>
</template>

<script>
import { ElMessageBox } from 'element-plus'
import ChatContent from "@/components/Home/ChatContent.vue"
import HomeFooter from "@/components/Home/HomeFooter.vue";
import HomeHeader from "@/components/Home/HomeHeader.vue";
import {ref,reactive,onMounted,nextTick} from 'vue'
export default {
  name: "Home",
  components: {
      HomeHeader,
      HomeFooter,
      ChatContent
  },
  setup() {
    //控制对话框是否打开,默认是关闭的
    let dialog = ref(false);
    //保存用户输入的内容
    let api = ref(null);

   //当用户点击确定的时候保存输入的内容到localstorage里
   //需要判断如果输入了内容，才能关闭对话框，否则不关闭
   const ckok = function(){
       if(api.value){
           localStorage.setItem("openai",api.value);
           dialog.value=false;
       }
   }

    //判断用户token里是否存在秘钥,不存在则打开弹窗
    const apiStatus = function(){
       let api =  localStorage.getItem('openai');
        if(!api){
            dialog.value = true;
        }
    }

    onMounted(()=>{
        // 调用判断秘钥是否存的方法
        apiStatus()
    })

    return {
        dialog,ckok,api
    }
  },
}
</script>

<style lang="less" scoped>
main{
    background: url("~@/assets/images/bg.png") no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 1rem;
    height: 100%;
    #Home{
        height: 100%;
        display: flex;
        flex-direction: column;
        max-width: 1140px;
   }
   .dialog{
       :deep(.el-dialog){
           width: 80%;
           max-width: 523px;
       }
   }
}
</style>
