import axios from "axios";

//导出基准地址，原因其他地方不是通过axios发送请求的地方也要用这个地址
export const baseURL = 'https://api.openai.com/v1/completions'
const instance = axios.create({
    //axios的一些配置 baseURL timeout
    baseURL,
    timeout:50000
});

//请求拦截器
instance.interceptors.request.use(config=>{
    //判断是否有tooke
    if(localStorage.getItem('openai')){
        let key = localStorage.getItem('openai');
       // 如果存在在请求头带上tooken
       config.headers.Authorization = `Bearer ${key}`;
    }

    //放行
    return config;
},err=>{
    //拦截失败做的事情
    return Promise.reject(err);
})

//响应拦截器
instance.interceptors.response.use(res=>{
    //剥离其他数据，只返回data数据
    return res.data
},
err=>{
    
    return Promise.reject(err);
});


//请求工具函数
const request = (url,method,submitData)=>{
    //负责发请求,请求地址，请求方式，提交数据,返回的是promise
    return instance({
        url,
        method,
        [method.toLowerCase()==='get'?'params':'data']:submitData,
    })
}
export default request