// 首页页脚功能
<template>
  <div id="HomeFooter">
      <div class="send">
          <input type="text" v-model="inputvalue" placeholder="请输入内容">
           <el-button @click="send">发送</el-button>
      </div>
  </div>
</template>

<script>
import {sendApi} from '@/api/robot';
import {storeToRefs} from 'pinia';
import {useMainStore} from "@/store/index";
import {ref,reactive,onMounted,nextTick} from 'vue';
export default {
  name: "HomeFooter",
  components: {
  },
  setup() {
      //实例化store
      const store = useMainStore();
      const {user} = storeToRefs(store);
      
      //存储用户输入的内容
      let inputvalue = ref('');

   
      //发送按钮调用的方法
      const send = async function(){
          //判断如果内容为空则不执行
          if(!inputvalue.value) return;
          //如果不为空先把输入的内容添加自己的对话中
          store.addUser(inputvalue.value);
        //   store.addRobot(inputvalue.value);
          let result = await sendApi({
          "prompt": inputvalue.value,
          "max_tokens": 2048,
          "temperature": 0.5,
          "top_p": 1,
          "frequency_penalty": 0,
          "presence_penalty": 0,
          "model": "text-davinci-003"
          });
         if(result.id!==undefined){
             store.addRobot(result.choices[0].text);
             console.log('执行了');
         }
          //清空聊天框
          inputvalue.value = "";
      }  


    return {
        inputvalue,send
    }
  },
}
</script>

<style lang="less" scoped>
#HomeFooter{
    margin-top: 1rem;
 .send{
     display: flex;
     input{
        padding-left: 1rem; 
        appearance: none;
        width: 248px;
        border: 0px solid #fff;
        outline: 0;
        letter-spacing: 1px;
        color: #fdb11a;
        font-weight: 600;
        background: rgba(45,45,45,.10);
        box-shadow: 0 2px 3px 0 rgba(0,0,0,.1) inset;
        text-shadow: 0 1px 2px rgba(0,0,0,.1);
        -o-transition: all .2s;
        -moz-transition: all .2s;
        -webkit-transition: all .2s;
        -ms-transition: all .2s;
        flex: 1;
     }
 }   
}
</style>
